import Vue from 'vue'
import VueRouter from 'vue-router'
import tvView from '../views/tvView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'tvView',
    component: tvView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
